import React from "react";
import { styled } from "baseui";
import Submenu from "./Submenu";
import { Streamlit } from "streamlit-component-lib";
import MenuIcon from "./MenuIcon";

const MenuBody = ({
  title,
  children,
  id,
  is_collapsible,
  activeMenuId,
  setActiveMenuId,
  active,
  submenu_style,
  single_menu_style,
  submenuParaentColor,
  setSubmenuParaentColor,
  icon,
  render_count
}) => {
  return (
    <>
      {children ? (
        <Submenu
          title={title}
          activeMenuId={activeMenuId}
          setActiveMenuId={setActiveMenuId}
          children={children}
          is_collapsible={is_collapsible}
          submenu_style={submenu_style}
          single_menu_style={single_menu_style}
          submenuParaentColor={submenuParaentColor}
          setSubmenuParaentColor={setSubmenuParaentColor}
          icon={icon}
          render_count={render_count}
        />
      ) : (
        <StyledMenuItem
          $active={active}
          title={title}
          main_menu_style={single_menu_style}
          onClick={() => {
            Streamlit.setComponentValue({title, render_count});
            setActiveMenuId(id);
            setSubmenuParaentColor(null);
          }}
        >
          <MenuIcon icon={icon} />
          {title}
        </StyledMenuItem>
      )}
    </>
  );
};

export default MenuBody;

const StyledMenuItem = styled("div", (props) => ({
  padding: "1.25rem 2rem",
  fontFamily: props.main_menu_style?.font_family
    ? props.main_menu_style?.font_family
    : "inherit",
  // background: props.$active
  //   ? props.main_menu_style?.activeMenu?.backgroundColor
  //     ? props.main_menu_style?.activeMenu?.backgroundColor
  //     : "#b4cdf0"
  //   : "none",
  color: props.$active
    ? props.main_menu_style?.active_menu?.color
      ? props.main_menu_style?.active_menu?.color
      : "blue"
    : props.main_menu_style?.color
    ? props.main_menu_style?.color
    : "black",
  display: "flex",
  alignItems: "center",
  justifyContent: "flex-start",
  fontSize: "1.1rem",
  fontWeight: "600",
  cursor: "pointer",
  width: "100%",
  borderLeft: props?.main_menu_style?.hover?.left_border
    ? "4px solid transparent"
    : "",
  ":hover": {
    background: props.main_menu_style?.hover?.background_color
      ? props.main_menu_style?.hover?.background_color
      : "#b4cdf0",
    color: props.main_menu_style?.hover?.color
      ? props.main_menu_style?.hover?.color
      : "blue",
    borderLeft: props?.main_menu_style?.hover?.left_border
      ? `4px solid ${
          props.main_menu_style?.hover?.color
            ? props.main_menu_style?.hover?.color
            : "blue"
        }`
      : "",
  },
}));
