import React, { useState } from "react";
import { styled } from "baseui";
import { useStyletron } from "styletron-react";
import { Streamlit } from "streamlit-component-lib";
import MenuIcon from "./MenuIcon";

const Submenu = ({
  title,
  children,
  is_collapsible,
  setActiveMenuId,
  activeMenuId,
  submenu_style,
  single_menu_style,
  setSubmenuParaentColor,
  submenuParaentColor,
  icon,
  render_count,
}) => {
  const [css] = useStyletron();
  const [open, setOpen] = useState(false);

  const checkParent = () => {
    let children_ids = children.map((obj) => obj.id);
    return children_ids.includes(activeMenuId);
  };

  return (
    <>
      <StyledSubMenuParent
        title={title}
        main_menu_style={single_menu_style}
        onClick={() => setOpen((flag) => !flag)}
        open={open}
        is_collapsible={is_collapsible.toString()}
        submenu_parent_color={checkParent() ? submenuParaentColor : null}
      >
        <div className={css({ display: "flex" })}>
          <MenuIcon icon={icon} />
          {title}
        </div>

        <div className={css({ display: is_collapsible ? "block" : "none" })}>
          {is_collapsible && open ? (
            <i className="fa-solid fa-angle-down"></i>
          ) : (
            <i className="fa-solid fa-angle-up"></i>
          )}
        </div>
      </StyledSubMenuParent>

      <div
        className={css({
          display: open || !is_collapsible ? "flex" : "none",
          paddingLeft: "30px",
          flexDirection: "column",
          overflow: "hidden",
          "@media (max-width: 768px)": {
            margin: "0 1.5rem",
          },
        })}
      >
        {children?.map(({ icon, title, id }, index) => (
          <StyledSubMenuItem
            $active={id === activeMenuId}
            title={title}
            submenu_style={submenu_style}
            key={index}
            onClick={() => {
              Streamlit.setComponentValue({ title, render_count });
              setActiveMenuId(id);
              setSubmenuParaentColor(single_menu_style?.hover?.color || "blue");
            }}
          >
            <MenuIcon icon={icon} />
            {title}
          </StyledSubMenuItem>
        ))}
      </div>
    </>
  );
};

export default Submenu;

const StyledSubMenuParent = styled("div", (props) => ({
  padding: "1.25rem 2rem",
  fontFamily: props.main_menu_style?.font_family
    ? props.main_menu_style?.font_family
    : "inherit",
  display: "flex",
  alignItems: "center",
  justifyContent: "space-between",
  fontSize: "1.1rem",
  fontWeight: "600",
  borderLeft: props?.main_menu_style?.hover?.left_border
    ? "4px solid transparent"
    : "",
  color: props.submenu_parent_color
    ? props.submenu_parent_color
    : props.main_menu_style.color
    ? props.main_menu_style.color
    : "black",
  cursor: props.is_collapsible === "true" ? "pointer" : "",
  ":hover": {
    color:
      props.is_collapsible === "true"
        ? props.main_menu_style?.hover?.color
          ? props.main_menu_style?.hover?.color
          : "blue"
        : "",
  },
}));

const StyledSubMenuItem = styled("div", (props) => ({
  padding: "1.25rem 0.8rem",
  fontFamily: props.submenu_style?.font_family
    ? props.submenu_style?.font_family
    : "inherit",
  // background: props.$active
  //   ? props.submenu_style?.active_submenu?.backgroundColor
  //     ? props.submenu_style?.active_submenu?.backgroundColor
  //     : "#b4cdf0"
  //   : "none",
  color: props.$active
    ? props.submenu_style?.active_submenu?.color
      ? props.submenu_style?.active_submenu?.color
      : "blue"
    : props?.submenu_style?.color
    ? props?.submenu_style?.color
    : "black",
  display: "flex",
  alignItems: "center",
  justifyContent: "flex-start",
  fontSize: "1.1rem",
  cursor: "pointer",
  width: "100%",
  borderLeft: props?.submenu_style?.hover?.left_border
    ? "4px solid transparent"
    : "",
  ":hover": {
    background: props.submenu_style?.hover?.background_color
      ? props.submenu_style?.hover?.background_color
      : "#b4cdf0",
    color: props.submenu_style?.hover?.color
      ? props.submenu_style?.hover?.color
      : "blue",
    borderLeft: props?.submenu_style?.hover?.left_border
      ? `4px solid ${
          props.submenu_style?.hover?.color
            ? props.submenu_style?.hover?.color
            : "blue"
        }`
      : "",
  },
}));
