import { useEffect } from "react";
import * as React from "react";
import { Streamlit, withStreamlitConnection } from "streamlit-component-lib";
import { styled, useStyletron } from "baseui";
import MenuBody from "./MenuBody";
import { StyledDivider, SIZE } from "baseui/divider";
import MenuHeader from "./MenuHeader";

const Menu = (props) => {
  const [activeMenuId, setActiveMenuId] = React.useState(null);
  const [submenuParaentColor, setSubmenuParaentColor] = React.useState(null);

  const {
    header,
    menu_items,
    wrapper_style,
    header_style,
    single_menu_style,
    submenu_style,
    divider_between_header_and_body,
    render_count,
  } = props.args;

  useEffect(() => {
    Streamlit.setFrameHeight(1000);
  }, []);

  const [css] = useStyletron();

  return (
    <MenuWrapper
      backgroundcolor={wrapper_style.background_color}
      color={wrapper_style.text}
    >
      <MenuHeader
        appLogo={header.logo}
        appName={header.title}
        styles={header_style}
      />

      {divider_between_header_and_body && (
        <div>
          <StyledDivider
            $size={SIZE.section}
            className={css({
              marginLeft: "1rem",
              marginRight: "1rem",
            })}
          />
        </div>
      )}

      {menu_items.map(({ icon, title, children, id }, index) => (
        <MenuBody
          key={index}
          active={id === activeMenuId}
          activeMenuId={activeMenuId}
          setActiveMenuId={setActiveMenuId}
          title={title}
          children={children}
          id={id}
          is_collapsible={props.args.is_collapsible}
          submenu_style={submenu_style}
          single_menu_style={single_menu_style}
          submenuParaentColor={submenuParaentColor}
          setSubmenuParaentColor={setSubmenuParaentColor}
          icon={icon}
          render_count={render_count}
        />
      ))}
    </MenuWrapper>
  );
};

export default withStreamlitConnection(Menu);

const MenuWrapper = styled("div", (props) => ({
  display: "flex",
  flexDirection: "column",
  width: "100%",
  height: "100vh",
  background: props.backgroundcolor
    ? props.backgroundcolor
    : "rgb(250, 250, 250)",
}));
